.form-fild {
  box-sizing: border-box;
  display: block;
  width: $width100;
  height: 48px;
  background-color: #f8f8fa;
  border-radius: 40px;
  border: 1px solid transparent;
  padding: 14px 16px;
  color: #292929;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;

  &::placeholder {
    font-weight: $fontwaight400;
    font-size: $fontsize;
    line-height: 20px;
    color: #7e7e83;
    font-family: "Roboto", sans-serif;
  }

  &:hover {
    border-color: #d5d7de;
  }

  &:focus {
    border-color: #292929;
    outline: none;
  }

  &--textarea {
    height: 148px;
    resize: none;
    color: #7e7e83;
    font-family: "Roboto", sans-serif;
  }
}
